import React from "react";

import axios from "axios";
import { useContext, useState } from "react";
import { useQuery } from "react-query";
import { Link } from "react-router-dom";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";

import { CartContext } from "../context/CartContext";
import toast, { Toaster } from "react-hot-toast";
export default function Product() {
  let [page, setpage] = useState(1);
  let { Addcart, setitemNum } = useContext(CartContext);

  function pagenumber(e) {
    let pageNum = e.target.getAttribute("pagenumber");
    setpage(pageNum);
  }

  let { isLoading, data } = useQuery([`productlist`, page], getProduct);

  function getProduct(queryData) {
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/products?page=${queryData.queryKey[1]}`
    );
  }

  async function AddIdCart(id) {
    let req = await Addcart(id).catch((errr) => {
      // console.log("heloo");
    });
    // console.log(req);
    if (req.data.status === "success") {
      toast.success(req.data.message);

      setitemNum(req.data.numOfCartItems);
    }
  }

  return (
    <div>
      {isLoading ? (
        <div className="loading position-fixed top-0 end-0 bottom-0 start-0 bg-white d-flex justify-content-center align-items-center ">
          <span className="loader "></span>
        </div>
      ) : (
        <div className="row g-3  my-5">
          <Toaster />

          {data?.data.data.map((e) => {
            return (
              <div key={e.id} className="col-md-3 h-100 ">
                <div className="product ">
                  <Link to={"/ProductDetails/" + e.id}>
                    <img src={e.imageCover} alt=" " className="w-100"></img>

                    {/* <img src={e.imageCover} alt="nd" className="w-100"></img> */}
                    <h6 className="text-success">{e?.category?.slug}</h6>
                    <h5>{e.title.split(" ").slice(0, 2).join(" ")}</h5>

                    <div className="d-flex justify-content-between ">
                      <p>{e.price} EGP</p>

                      <span>
                        <i className="fa-solid fa-star text-warning"></i>
                        {e.ratingsAverage}
                      </span>
                    </div>
                  </Link>
                  <div className="">
                    <button
                      className="bg-main text-white btn w-100 d-block"
                      onClick={() => {
                        AddIdCart(e.id);
                      }}
                    >
                      add product
                    </button>
                  </div>
                </div>
              </div>
            );
          })}

          <nav aria-label="Page navigation example ">
            <ul className="pagination justify-content-center mt-5">
              <li className="page-item ">
                <Link className="page-link" onClick={pagenumber} pagenumber="1">
                  1
                </Link>
              </li>
              <li className="page-item">
                <Link className="page-link" onClick={pagenumber} pagenumber="2">
                  2
                </Link>
              </li>
            </ul>
          </nav>
        </div>
      )}
    </div>
  );
}
