import axios from "axios";
import { useFormik } from "formik";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import * as Yup from "yup";
export default function ForgetPassword() {
  let [code, setcode] = useState(false);
  let [errmsgE, seterrE] = useState("");
  let [errmsgC, seterrC] = useState("");
  let [spinmsg, setspin] = useState(true);
  let navg = useNavigate();

  let validcode = Yup.object({
    resetCode: Yup.string()
      .required("code Required")
      .matches(/^[0-9]{3,9}$/),
  });

  async function sendcode(valu) {
    setspin(false);
    let req = await axios
      .post(`https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode`, valu)
      .catch(function (err) {
        seterrC(err.response.data.message);
      });
    setspin(true);

    if (req.data.status === "Success") {
      navg("/ResetPassword");
    }

    // console.log(req);
  }

  let form2 = useFormik({
    initialValues: {
      resetCode: "",
    },
    onSubmit: sendcode,
    validationSchema: validcode,
  });

  let validemail = Yup.object({
    email: Yup.string().required("email Required").email("enter valid email"),
  });

  async function sendemail(valu) {
    setspin(false);
    let req = await axios
      .post(`https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords`, valu)
      .catch(function (err) {
        seterrE(err.response.data.message);
      });
    // console.log(req);
    setspin(true);

    if (req.data.statusMsg === "success") {
      setcode(true);
    }
  }

  let form1 = useFormik({
    initialValues: {
      email: "",
    },

    onSubmit: sendemail,
    validationSchema: validemail,
  });

  return (
    <div>
      {code === false ? (
        <div  className="my-3">
          {errmsgE !== "" ? (
            <div className="alert alert-danger">{errmsgE}</div>
          ) : (
            ""
          )}
          <form onSubmit={form1.handleSubmit}>
            <label htmlFor="email">Email.....</label>
            <input
              className="form-control"
              type="email"
              name="email"
              id="email"
              onChange={form1.handleChange}
              onBlur={form1.handleBlur}
            ></input>
            {form1.errors.email && form1.touched.email ? (
              <div className="alert alert-danger">{form1.errors.email}</div>
            ) : (
              ""
            )}
            <div className="d-flex justify-content-end mt-3">
              {spinmsg === true ? (
                <button
                  className="btn btn-success"
                  type="submit"
                  disabled={!(form1.dirty && form1.isValid)}
                >
                  Send
                </button>
              ) : (
                <button className="btn btn-success">
                  <i className="fa-solid fa-spinner fa-spin text-white"></i>
                </button>
              )}
            </div>
          </form>
        </div>
      ) : (
        <>
          <div className="my-3">
            {errmsgC !== "" ? (
              <div className="alert alert-danger">{errmsgC}</div>
            ) : (
              ""
            )}
            <form onSubmit={form2.handleSubmit}>
              <label htmlFor="ResetCode">ResetCode.....</label>
              <input
                className="form-control"
                type="text"
                name="resetCode"
                id="ResetCode"
                value={form2.values.resetCode}
                onChange={form2.handleChange}
                onBlur={form2.handleBlur}
              ></input>
              {form2.errors.resetCode && form2.touched.resetCode ? (
                <div className="alert alert-danger">
                  {form2.errors.resetCode}
                </div>
              ) : (
                ""
              )}
              <div className="d-flex justify-content-end mt-3">
                {spinmsg === true ? (
                  <button
                    className="btn btn-success"
                    type="submit"
                    disabled={!(form2.dirty && form2.isValid)}
                  >
                    Send
                  </button>
                ) : (
                  <button className="btn btn-success">
                    <i className="fa-solid fa-spinner fa-spin text-white"></i>
                  </button>
                )}
              </div>
            </form>
          </div>
        </>
      )}
    </div>
  );
}
