import React, { useContext, useEffect } from "react";
import Layout from "./Layout/Layout";
import Home from "./Homecomponannt/Home";
import Product from "./Product/Product";

import Cart from "./cart/Cart";
import Login from "./componant/Login";
import { RouterProvider,  createHashRouter } from "react-router-dom";
import Register from "./componant/Register";
import NotFound from "./componant/NotFound";
import { Usernamecontext } from "./context/usercontext";
import Protect from "./componant/protect";
import { jwtDecode } from "jwt-decode";
import ForgetPassword from "./componant/ForgetPassword";
import ResetPassword from "./componant/ResetPassword";
import { QueryClient, QueryClientProvider } from "react-query";
import ProductDetails from "./Product/ProductDetails";
import { CartContextProvider } from "./context/CartContext";
import Brand from "./Brands/Brand";
import Start from "./componant/Start";
import CheckOut from "./CheckOut/CheckOut";
import Allorder from "./Allorder/Allorder";
import Wishlist from "./Wishlist/Wishlist";

export default function App() {
  let queryclient = new QueryClient();

  let routers = createHashRouter([
    {
      path: "",
      element: <Layout />,
      children: [
        {
          path: "home",
          element: (
            <Protect>
              <Home />
            </Protect>
          ),
        },
        {
          path: "ProductDetails/:id",
          element: (
            <Protect>
              <ProductDetails />
            </Protect>
          ),
        },
        {
          path: "Wishlist",
          element: (
            <Protect>
              <Wishlist />
            </Protect>
          ),
        },
        {
          path: "brand",
          element: (
            <Protect>
              <Brand />
            </Protect>
          ),
        },
        {
          path: "Product",
          element: (
            <Protect>
              <Product />
            </Protect>
          ),
        },
        {
          path: "CheckOut/:id",
          element: (
            <Protect>
              <CheckOut />
            </Protect>
          ),
        },
        {
          path: "Allorders",
          element: (
            <Protect>
              <Allorder />
            </Protect>
          ),
        },
        {
          index: true,
          element: <Start></Start>,
        },
        { path: "forgetpassword", element: <ForgetPassword></ForgetPassword> },
        { path: "ResetPassword", element: <ResetPassword></ResetPassword> },

 
        {
          path: "Cart",
          element: (
            <Protect>
              <Cart />
            </Protect>
          ),
        },
        { path: "Login", element: <Login /> },
        { path: "Register", element: <Register /> },

        { path: "*", element: <NotFound /> },
      ],
    },
  ]);
  let { setToken, setname } = useContext(Usernamecontext);


  useEffect(() => {
    if (localStorage.getItem("userdate") != null) {
      setToken(localStorage.getItem("userdate"));
      setname(jwtDecode(localStorage.getItem("userdate")));
    
    }
  }, [setToken, setname]);
  return (
    <QueryClientProvider client={queryclient}>
    
        <CartContextProvider>
          <RouterProvider router={routers}></RouterProvider>
        </CartContextProvider>
      
    </QueryClientProvider>
  );
}
