import React, { useContext } from "react";
import { NavLink, Link, useNavigate } from "react-router-dom";
import logo from "../assest/freshcart-logo.svg";
import { Usernamecontext } from "../context/usercontext";
import { CartContext } from "../context/CartContext";

export default function Navbar() {
  let { userToken, setToken } = useContext(Usernamecontext);
  let { itemNum } = useContext(CartContext);
  let navlogout = useNavigate();

  function logut() {
    navlogout("/login");
    localStorage.removeItem("userdate");
    setToken(null);
  }
  return (
    <div>
      <nav className="fixed-top navbar navbar-expand-lg bg-body-secondary">
        <div className="container-fluid">
          <Link className="navbar-brand" to={"home"}>
            <img src={logo} alt="" />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            {userToken == null ? (
              ""
            ) : (
              <ul className="navbar-nav me-auto mb-2 text-center mb-lg-0">
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive === true
                        ? "nav-link text-success fw-bolder"
                        : "nav-link"
                    }
                    aria-current="page"
                    to={"home"}
                  >
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive === true
                        ? "nav-link text-success fw-bolder"
                        : "nav-link"
                    }
                    to={"Product"}
                  >
                    product
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive === true
                        ? "nav-link text-success fw-bolder"
                        : "nav-link"
                    }
                    to={"brand"}
                  >
                    Brands
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive === true
                        ? "nav-link text-success fw-bolder"
                        : "nav-link"
                    }
                    to={"Cart"}
                  >
                    cart
                  </NavLink>
                </li>

                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive === true
                        ? "nav-link text-success fw-bolder"
                        : "nav-link"
                    }
                    to={"wishlist"}
                  >
                    WishList
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive === true
                        ? "nav-link text-success fw-bolder"
                        : "nav-link"
                    }
                    to={"allorders"}
                  >
                    AllOrder
                  </NavLink>
                </li>
              </ul>
            )}
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0 text-center">
              {userToken == null ? (
                <>
                  <li className="nav-item">
                    <NavLink
                      className={({ isActive }) =>
                        isActive === true
                          ? "nav-link text-success fw-bolder"
                          : "nav-link"
                      }
                      to={"/login"}
                    >
                      login
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink
                      className={({ isActive }) =>
                        isActive === true
                          ? "nav-link text-success fw-bolder"
                          : "nav-link"
                      }
                      to={"Register"}
                    >
                      Register
                    </NavLink>
                  </li>
                </>
              ) : (
                <>
                  <li className="nav-item  py-2   ">
                    {itemNum != null ? (
                      <Link to={"cart"}>
                        <div className="position-relative pe-3 nav-item">
                          <i className="fa-solid fa-cart-shopping  ms-1 text-main"></i>
                          <h6 className=" fw-bolder text-danger rounded-5 fs-6  position-absolute  top-0 end-0 ">
                            {itemNum}
                          </h6>
                        </div>
                      </Link>
                    ) : (
                      <></>
                    )}
                  </li>

                  <li className="nav-item">
                    <NavLink
                      className={({ isActive }) =>
                        isActive === true
                          ? "nav-link text-success fw-bolder"
                          : "nav-link"
                      }
                      onClick={logut}
                      to={"/login"}
                    >
                      Logout
                    </NavLink>
                  </li>
                </>
              )}
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}
