import React, { useContext, useState } from "react";
import { WishlistContextprover } from "../context/Wishlistcontext ";
import { useEffect } from "react";
import { Link } from "react-router-dom";
import { CartContext } from "../context/CartContext";
import toast, { Toaster } from "react-hot-toast";

export default function Wishlist() {
  let { getuserwishlist, removeitemwishlist } = useContext(
    WishlistContextprover
  );
  let { Addcart, setitemNum } = useContext(CartContext);
  let [loading2, setloading2] = useState(false);

  let [Wishlist, sendwishlist] = useState([]);

  useEffect(() => {
    showwishlist();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  async function AddIdCart(id) {
    let req = await Addcart(id).catch((errr) => {
      // console.log("heloo");
    });
    // console.log(req);
    if (req.data.status === "success") {
      toast.success(req.data.message);
      setitemNum(req.data.numOfCartItems);
    }
  }

  async function showwishlist() {
    setloading2(true);
    let req = await getuserwishlist().catch((err) => {
      // console.log(err);
    });
    setloading2(false);
    // console.log(req.data.data);
    sendwishlist(req.data.data);
  }

  async function removeitem(id) {
    setloading2(true);

    let req = await removeitemwishlist(id).catch(() => {});
    sendwishlist(req?.data.data);
    // console.log(req.data);

    setloading2(false);
    showwishlist();
  }

  //   async function AddWishlist(id) {
  //     let req=await AddWishlist().cs

  // }

  return (
    <div>
      <Toaster />

      {loading2 === true ? (
        <div className="loading position-fixed top-0 end-0 bottom-0 start-0 bg-white d-flex justify-content-center align-items-center ">
          <span className="loader "></span>
        </div>
      ) : (
        <div className="row">
          {Wishlist.map((e) => {
            return (
              <div key={e.id} className="col-md-2 h-100 position-relative my-4">
                <div className="product ">
                  <div className=" position-absolute top-0 end-0 p-4">
                    <i
                      className="fa-solid fa-heart-crack fs-1 text-danger"
                      id="icon"
                      onClick={() => {
                        removeitem(e.id);
                      }}
                    ></i>
                  </div>

                  <Link to={"/ProductDetails/" + e.id}>
                    <img src={e.imageCover} alt=" " className="w-100"></img>

                    {/* <img src={e.imageCover} alt="nd" className="w-100"></img> */}
                    {/* <h6 className="text-success">{e?.category?.slug}</h6> */}
                    <h5>{e?.title?.split(" ").slice(0, 2).join(" ")}</h5>

                    <div className="d-flex justify-content-between ">
                      <p>{e.price} EGP</p>

                      {/* <span>
                        <i className="fa-solid fa-star text-warning"></i>
                        {e.ratingsAverage}
                      </span> */}
                    </div>
                  </Link>
                  <div className="">
                    <button
                      className="bg-main text-white btn w-100 d-block"
                      onClick={() => {
                        AddIdCart(e.id);
                      }}
                    >
                      add product
                    </button>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
}
