import React from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import img1 from "../assest/slider-image-1.jpeg";
import img2 from "../assest/slider-image-2.jpeg";
import img3 from "../assest/slider-image-3.jpeg";

export default function MainCarousel() {
  return (
    <>
      <div className="row g-0">
        <div className="col-md-9">
          <OwlCarousel
            items={1}
            loop
            className="owl-carousel owl-theme"
            autoplay
            autoplayTimeout={3000}
          >
            <div className="item">
              <img src={img2} alt="" height={400}></img>
            </div>

            <div className="item">
              <img src={img1} alt="" height={400}></img>
            </div>
            <div className="item">
              <img src={img3} alt="" height={400}></img>
            </div>
          </OwlCarousel>
        </div>
        <div className="col-md-3">
          <img src={img2} alt="" className="w-100" height={200}></img>
          <img src={img3} alt="" className="w-100" height={200}></img>
        </div>
      </div>
    </>
  );
}
