import axios from "axios";
import React from "react";
import { useQuery } from "react-query";

export default function Brand() {
  let { data, isLoading } = useQuery("brand", showbrand);

  async function showbrand() {
    return await axios.get(`https://ecommerce.routemisr.com/api/v1/brands`);
  }
  let product = data?.data.data;
  // console.log(product);

  return (
    <div>
      {isLoading ? (
        <div className="loading position-fixed top-0 end-0 bottom-0 start-0 bg-white d-flex justify-content-center align-items-center ">
          <span className="loader "></span>
        </div>
      ) : (
        <div className="row g-3 ">
          {product?.map((el) => {
            return (
              <div key={el._id} className="col-md-2">
                <img src={el.image} alt="" className="w-100"></img>
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
}
