import { useFormik } from "formik";
import React, { useContext, useState } from "react";
import * as Yup from "yup";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { Usernamecontext } from "../context/usercontext";
import { jwtDecode } from "jwt-decode";
 

export default function Login() {
  
  let { setname } = useContext(Usernamecontext);
  let { setToken } = useContext(Usernamecontext);
  let navg = useNavigate();
  let [errmsg, seterr] = useState("");
  let [spin, setspin] = useState(true);
  let valid = Yup.object({
    email: Yup.string().required("email Required").email("enter valid email"),
    password: Yup.string()
      .required("password Required")
      .matches(/^[A-Za-z0-9#$%-@^_&*]{6,20}$/, "enter Valid Password"),
  });
  async function loginapi(valu) {
    setspin(false);
    let req = await axios
      .post(`https://ecommerce.routemisr.com/api/v1/auth/signin`, valu)
      .catch(function (err) {
        seterr(err.response.data.message);
        setspin(true);
      });
    // console.log(req);
    if (req.data.message === "success") {
      setspin(true);
      setToken(req.data.token);
      localStorage.setItem("userdate", req.data.token);
      setname(jwtDecode(req.data.token));
      navg("/home");
    }
  }

  let formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    onSubmit: loginapi,
    validationSchema: valid,
  });

  return (
    <div className="mt-5">
      <h2>login Now.....</h2>
      {errmsg === "" ? (
        ""
      ) : (
        <div className="alert alert-danger">
          <p>{errmsg}</p>
        </div>
      )}
      <form onSubmit={formik.handleSubmit}>
        <div className="my-3">
          <label htmlFor="email">Email:</label>
          <input
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            type="email"
            name="email"
            id="email"
            className="form-control"
          ></input>
          {formik.touched.email && formik.errors.email ? (
            <div className="alert alert-danger">
              <p>{formik.errors.email}</p>
            </div>
          ) : (
            ""
          )}
        </div>
        <div className="my-3">
          <label htmlFor="password">password:</label>
          <input
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            type="password"
            name="password"
            id="password"
            className="form-control"
          ></input>
          {formik.dirty.password && formik.errors.password ? (
            <div className="alert alert-danger">
              <p>{formik.errors.password}</p>
            </div>
          ) : (
            ""
          )}
        </div>
        <div className="d-flex justify-content-end nav-link click">
          <Link to={"/ForgetPassword"} className="nav-link">
            ForgetPassword?......
          </Link>
        </div>
        <div className="d-flex justify-content-end">
          {spin === true ? (
            <button
              type="submit"
              className="btn btn-success"
              disabled={!(formik.isValid && formik.dirty)}
            >
              login
            </button>
          ) : (
            <button className="btn btn-success">
              <i className="fa-solid fa-spinner fa-spin text-white"></i>
            </button>
          )}
        </div>
      </form>
    </div>
  );
}
