import React, { useState } from "react";
import { useFormik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { useNavigate } from "react-router-dom";
export default function Register() {
  let navgate = useNavigate();

  let [errmsg, seterr] = useState("");
  let [spin, setspin] = useState(true);

  let valid = Yup.object({
    name: Yup.string()
      .max(20, "max Char 20")
      .min(3, "min Char 3")
      .required("name Required"),
    email: Yup.string().required("email Required").email("Enter Valid Email"),
    password: Yup.string()
      .matches(/^[A-Za-z0-9#$%-@^_&*]{6,20}$/, "enter Valid Password")
      .required("Password Required"),
    rePassword: Yup.string()
      .oneOf([Yup.ref("password")], "enter confirm Password")
      .required("rePassword Required"),
    phone: Yup.string()
      .matches(/^01[1025][0-9]{8}$/, "enter Valid Phone")
      .required("phone Required"),
  });

  async function form(valu) {
    setspin(false);
    let req = await axios
      .post("https://ecommerce.routemisr.com/api/v1/auth/signup", valu)
      .catch(function (errr) {
        seterr(errr.response.data.message);
        setspin(true);
      });
    if (req.data.message === "success") {
      setspin(true);
      navgate("/Login");
    }
  }
  let formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      password: "",
      rePassword: "",
      phone: "",
    },
    onSubmit: form,
    validationSchema: valid,
  });

  return (
    <div className="mt-5">
      <h2>Register Now:</h2>
      {errmsg === "" ? (
        ""
      ) : (
        <div className="alert alert-danger">
          <p>{errmsg}</p>
        </div>
      )}

      <form onSubmit={formik.handleSubmit}>
        <div>
          <label htmlFor="name">Name:</label>
          <input
            type="text"
            className="form-control"
            name="name"
            id="name"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          ></input>

          {formik.touched.name && formik.errors.name ? (
            <div className="alert alert-danger">
              <p className="text-danger">{formik.errors.name} </p>
            </div>
          ) : (
            ""
          )}
        </div>
        <div>
          <label htmlFor="email">email:</label>
          <input
            type="email"
            className="form-control"
            name="email"
            id="email"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          ></input>
          {formik.touched.email && formik.errors.email ? (
            <div className="alert alert-danger">
              <p className="text-danger">{formik.errors.email} </p>
            </div>
          ) : (
            ""
          )}
        </div>
        <div>
          <label htmlFor="phone">phone:</label>
          <input
            type="tel"
            className="form-control"
            name="phone"
            id="phone"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          ></input>
          {formik.touched.phone && formik.errors.phone ? (
            <div className="alert alert-danger">
              <p className="text-danger">{formik.errors.phone} </p>
            </div>
          ) : (
            ""
          )}
        </div>
        <div>
          <label htmlFor="password">password:</label>
          <input
            type="password"
            className="form-control"
            name="password"
            id="password"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          ></input>
          {formik.touched.password && formik.errors.password ? (
            <div className="alert alert-danger">
              <p className="text-danger">{formik.errors.password} </p>
            </div>
          ) : (
            ""
          )}
        </div>
        <div>
          <label htmlFor="repassword">repassword:</label>
          <input
            type="password"
            className="form-control"
            name="rePassword"
            id="repassword"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          ></input>
          {formik.touched.rePassword && formik.errors.rePassword ? (
            <div className="alert alert-danger">
              <p className="text-danger">{formik.errors.rePassword} </p>
            </div>
          ) : (
            ""
          )}
        </div>

        <div className=" d-flex justify-content-end mt-3">
          {spin === true ? (
            <button
              className="btn btn-success  "
              type="submit"
              disabled={!(formik.isValid && formik.dirty)}
            >
              Register
            </button>
          ) : (
            <buttom className="btn btn-success">
              <i className="fa-solid fa-spinner fa-spin text-white"></i>
            </buttom>
          )}
        </div>
      </form>
    </div>
  );
}
