import React, { useContext, useEffect, useState } from "react";
import { CartContext } from "../context/CartContext";
import { Link } from "react-router-dom";

export default function Cart() {
  let {
    Getusercart,
    removeitemcart,
    setitemNum,
    clearitemcart,
    updateitemcart,
  } = useContext(CartContext);
  let [loading, setloading] = useState(true);
  let [loading2, setloading2] = useState(false);
  let [cartlist, sendcartlist] = useState(null);

  useEffect(() => {
    showcart();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  async function deleteitem(id) {
    setloading2(true);
    let req = await removeitemcart(id);
    if (req.data.status === "success") {
      sendcartlist(req.data.data);
      setitemNum(req.data.numOfCartItems);
      setloading2(false);
    }
  }
  async function clearcart() {
    let req = await clearitemcart().catch((errr) => {
      // console.log("errr");
    });
    // console.log(req);

    if (req?.data?.message === "success") {
      sendcartlist(null);
      setitemNum(null);
    }
  }

  async function updatecart(id, count) {
    let req = await updateitemcart(id, count);
    // console.log(req);
    if (count === 0) {
      deleteitem(id);
    } else {
      sendcartlist(req.data.data);
    }
  }

  async function showcart() {
    setloading(true);
    let req = await Getusercart().catch((err) => {
      if (err.response.data.statusMsg === "fail") {
        sendcartlist(null);
        setloading(false);
      }
    });

    if (req?.data.status === "success") {
      // console.log(req.data.data);
      sendcartlist(req.data.data);
      setloading(false);
    }
  }

  return (
    <div className="mt-5">
      {loading === true ? (
        <div className="loading position-fixed top-0 end-0 bottom-0 start-0 bg-white d-flex justify-content-center align-items-center ">
          <span className="loader "></span>
        </div>
      ) : (
        <div>
          {cartlist == null ? (
            <div className="alert alert-danger">Empty Cart</div>
          ) : (
            <div>
              <button
                className="btn btn-danger btn-sm float-end"
                onClick={clearcart}
              >
                Empty Cart
              </button>
              <div className="clearfix"></div>
              {cartlist?.products.map((el) => {
                return (
                  <>
                    <div className="row align-items-center border-3 border-bottom   ">
                      <div className="col-md-10 py-2  ">
                        <div className="row align-items-center ">
                          <div className="col-md-2">
                            <img
                              key={el.product.title}
                              src={el.product.imageCover}
                              alt=""
                              className="w-100"
                            ></img>
                          </div>
                          <div className="col-md-10">
                            <h6> {el.product.title}</h6>

                            <h6> price:{el.price}EGP</h6>

                            {loading2 === true ? (
                              <div className="loading position-fixed top-0 end-0 bottom-0 start-0 bg-white d-flex justify-content-center align-items-center ">
                              <span className="loader "></span>
                            </div>
                            ) : (
                              <button
                                onClick={() => {
                                  deleteitem(el.product.id);
                                }}
                                className="btn btn-danger btn-sm"
                              >
                                Remove <i className="fa-solid fa-trash"></i>
                              </button>
                            )}
                          </div>
                        </div>
                      </div>
                      <div className="col-md-2 my-5">
                        <span>
                          <button
                            className="btn btn-success btn-sm"
                            onClick={() => {
                              updatecart(el.product.id, (el.count += 1));
                            }}
                          >
                            <i className="fa-solid fa-plus"></i>{" "}
                          </button>
                        </span>
                        <span>
                          <button className="btn  btn-sm">{el.count}</button>
                        </span>
                        <span>
                          <button
                            className="btn btn-danger btn-sm"
                            onClick={() => {
                              updatecart(el.product.id, (el.count -= 1));
                            }}
                          >
                            <i className="fa-solid fa-minus"></i>
                          </button>
                        </span>
                      </div>
                    </div>
                  </>
                );
              })}
              <h2 className="text-main">
                Total Price:{cartlist?.totalCartPrice}
              </h2>
              <Link
                to={"/checkout/" + cartlist._id}
                className="btn btn-success"
              >
                Check Out Payment
              </Link>
            </div>
          )}
        </div>
      )}
    </div>
  );
}
