import axios from "axios";
import { useFormik } from "formik";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import * as Yup from "yup";

export default function ResetPassword() {
  let [Spinmsg, SetSpin] = useState(true);
  let [errmsg, Seterr] = useState("");
  let navg = useNavigate();

  let valid = Yup.object({
    email: Yup.string().required("email Required").email(),
    newPassword: Yup.string()
      .required("newPassword Required")
      .matches(/^[a-zA-Z0-9#$%-@^_&*]{6,20}$/),
  });

  async function reset(valu) {
    SetSpin(false);
    let req = await axios
      .put(`https://ecommerce.routemisr.com/api/v1/auth/resetPassword`, valu)
      .catch(function (err) {
        // console.log(err.message);
        Seterr(err.message);
      });

    if (req.data.token) {
      navg("/login");
    }

    SetSpin(true);
    // console.log(req);
  }

  let form = useFormik({
    initialValues: {
      email: "",
      newPassword: "",
    },
    onSubmit: reset,
    validationSchema: valid,
  });

  return (
    <div>
      <h2>ResetPassword.......</h2>

      {errmsg === "" ? "" : <div className="alert alert-danger">{errmsg}</div>}

      <form onSubmit={form.handleSubmit}>
        <div className="my-3">
          <label htmlFor="email">Email</label>
          <input
            className="form-control"
            type="email"
            name="email"
            id="email"
            onChange={form.handleChange}
            onBlur={form.handleBlur}
          ></input>

          {form.touched.email && form.errors.email ? (
            <div className="alert alert-danger">{form.errors.email}</div>
          ) : (
            ""
          )}
        </div>
        <div className="my-3">
          <label htmlFor="newPassword">NewPassword</label>
          <input
            className="form-control"
            type="password"
            name="newPassword"
            id="newPassword"
            onChange={form.handleChange}
            onBlur={form.handleBlur}
          ></input>

          {form.touched.newPassword && form.errors.newPassword ? (
            <div className="alert alert-danger">{form.errors.newPassword}</div>
          ) : (
            ""
          )}
        </div>
        <div className="d-flex justify-content-end mt-3">
          {Spinmsg === true ? (
            <button
              className="btn btn-success"
              type="Submit"
              disabled={!(form.dirty && form.isValid)}
            >
              send
            </button>
          ) : (
            <button className="btn btn-success">
              <i className="fa-solid fa-spinner fa-spin text-white"></i>
            </button>
          )}
        </div>
      </form>
    </div>
  );
}
