import axios from "axios";
import { createContext, useState } from "react";

export let CartContext = createContext();

export function CartContextProvider({ children }) {
  let [itemNum, setitemNum] = useState(0);

  function Getusercart() {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`, {
      headers: {
        token: localStorage.getItem("userdate"),
      },
    });
  }
  function removeitemcart(id) {
    return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${id}`, {
      headers: {
        token: localStorage.getItem("userdate"),
      },
    });
  }
  function clearitemcart() {
    return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`, {
      headers: {
        token: localStorage.getItem("userdate"),
      },
    });
  }

  function updateitemcart(id, count) {
    let body = {
      count: count,
    };
    return axios.put(
      `https://ecommerce.routemisr.com/api/v1/cart/${id}`,
      body,
      {
        headers: {
          token: localStorage.getItem("userdate"),
        },
      }
    );
  }

  function chekout(id, valu) {
    let data = {
      shippingAddress: valu,
    };

    return axios.post(
      `https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=http://localhost:3000`,
      data,
      {
        headers: {
          token: localStorage.getItem("userdate"),
        },
      }
    );
  }

  function Addcart(id) {
    let body = {
      productId: id,
    };
    return axios.post(`https://ecommerce.routemisr.com/api/v1/cart`, body, {
      headers: {
        token: localStorage.getItem("userdate"),
      },
    });
  }

  return (
    <CartContext.Provider
      value={{
        chekout,
        Addcart,
        setitemNum,
        itemNum,
        removeitemcart,
        Getusercart,
        clearitemcart,
        updateitemcart,
      }}
    >
      {children}
    </CartContext.Provider>
  );
}
