import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { useQuery } from "react-query";
import { useParams } from "react-router-dom";
import { CartContext } from "../context/CartContext";
import OwlCarousel from "react-owl-carousel";
import toast, { Toaster } from "react-hot-toast";

export default function ProductDetails() {
  let date = useParams();

  let { Addcart, setitemNum } = useContext(CartContext);

  let [productId, setProductId] = useState([]);

  // function getsrc(e) {
  //   let imgpath = e.target.getAttribute("src");

  //   document.querySelector(".imgcover").setAttribute("src", imgpath);
  // }

  useEffect(() => {
    setProductId(date?.id);
  }, [date?.id]);

  let { data, isLoading } = useQuery(["productdetails", productId], getdetails);
  function getdetails(query) {
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/products/${query.queryKey[1]}`
    );
  }
  let product = data?.data.data;
  // console.log(product);
  // console.log(isLoading);
  async function AddIdCart(id) {
    let req = await Addcart(id).catch((errr) => {
      // console.log("errr");
    });
    // console.log(req);
    if (req?.data?.status === "success") {
      toast.success(req?.data?.message);

      setitemNum(req?.data?.numOfCartItems);
    }
  }

  return (
    <div className="mt-5">
      {isLoading ? (
        <div className="loading position-fixed top-0 end-0 bottom-0 start-0 bg-white d-flex justify-content-center align-items-center ">
          <span className="loader "></span>
        </div>
      ) : (
        <div className="row pt-5 align-items-center">
          <Toaster />

          <div className="col-md-3">
            {/* <img
              src={product?.imageCover}
              className="w-100 imgcover"
              alt=""
            ></img> */}
            <div className="row w-100 justify-content-center g-0  ">
              <OwlCarousel
                loop
                autoplay
                autoplayTimeout={1000}
                items={1}
                className="owl-carousel owl-theme"
              >
                {product?.images?.map((el) => {
                  return (
                    <img
                      key={el}
                      src={el}
                      alt=""
                      // onClick={getsrc}
                      className=" w-100 img mx-auto mt-3 "
                    ></img>
                  );
                })}
              </OwlCarousel>
            </div>
          </div>

          <div className="col-md-9  ">
            <div>
              <h3>{product?.title}</h3>
              <h6 className="text-muted"> {product?.description}</h6>
            </div>
            <div>
              <h6 className="text-main">{product?.category?.slug}</h6>
            </div>
            <div className="d-flex justify-content-between">
              <p>{product?.price} EGP</p>
              <p>
                {" "}
                <i className="fa-solid fa-star text-warning"></i>
                {product?.ratingsAverage}
              </p>
            </div>

            <button
              className="bg-main text-white btn w-100 d-block"
              onClick={() => {
                AddIdCart(product?.id);
              }}
            >
              add product
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
