import React, { useEffect, useState } from "react";
import axios from "axios";
import { useQuery } from "react-query";
import { jwtDecode } from "jwt-decode";

export default function Allorder() {
  let [cartownerid, sendcartid] = useState();
  useEffect(() => {
    sendcartid(id);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  let toke = localStorage.getItem("userdate");
  let { id } = jwtDecode(toke);
  // console.log(jwtDecode(toke));
  // console.log(id);

  let { data, isLoading } = useQuery(["allorder", cartownerid], showallorder);
  function showallorder(query) {
    return axios
      .get(
        `https://ecommerce.routemisr.com/api/v1/orders/user/${query.queryKey[1]}`
      )
      .catch((err) => {});
  }
  // console.log(data?.data);

  return (
    <div className="">
      {isLoading ? (
        <div className="loading position-fixed top-0 end-0 bottom-0 start-0 bg-white d-flex justify-content-center align-items-center ">
          <span className="loader "></span>
        </div>
      ) : (
        <div className="row my-5 ">
          {data?.data.map((el) => {
            return (
              <div className="col-md-6 py-3" key={el.id}>
                <div className="dropdown-center">
                  <button
                    className="btn btn-success dropdown-toggle w-100 my-4"
                    type="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    {el.createdAt.slice(0, 10)}
                  </button>
                  <ul className="dropdown-menu w-100 px-5">
                    <div className=" border-bottom border-3 d-flex justify-content-between">
                      <h6>Name: {el.user.name}</h6>
                      <h6>Phone: {el.user.phone}</h6>{" "}
                    </div>
                    {el.cartItems.map((event) => {
                      return (
                        <div className="row my-3 align-items-center">
                          <div className="col-md-3">
                            <img
                              src={event?.product.imageCover}
                              alt=""
                              className="w-100"
                            ></img>
                          </div>
                          <div className="col-md-9 ">
                            <h6>
                              Name:
                              {event?.product.title
                                .split(" ")
                                .slice(0, 2)
                                .join(" ")}
                            </h6>
                            <h6 className="text-danger">
                              {" "}
                              <span> price:</span> {event.price}
                            </h6>
                          </div>
                        </div>
                      );
                    })}
                    <h6 className="fw-bolder text-main">
                      totalprice:
                      {el.totalOrderPrice}
                    </h6>
                  </ul>
                </div>
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
}
