import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { useQuery } from "react-query";
import { Link } from "react-router-dom";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import MainCarousel from "./MainCarousel";
import CateCarousel from "./CateCarousel";
import { CartContext } from "../context/CartContext";
import toast from "react-hot-toast";
import { WishlistContextprover } from "../context/Wishlistcontext ";
import { Usernamecontext } from "../context/usercontext";

export default function Home() {
  let [page, setpage] = useState(1);
  let { Addcart, setitemNum } = useContext(CartContext);
  let { AddWishlist } = useContext(WishlistContextprover);
  let date = useContext(Usernamecontext);

  function pagenumber(e) {
    let pageNum = e.target.getAttribute("pagenumber");
    setpage(pageNum);
  }

  let { isLoading, data } = useQuery([`productlist`, page], getProduct);

  function getProduct(queryData) {
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/products?page=${queryData.queryKey[1]}`
    );
  }
  // console.log("data", data);

  useEffect(() => {
    toast.success("hello:"+ date?.username.name);
  }, [date?.username.name]);
  async function AddIdCart(id) {
    let req = await Addcart(id).catch((errr) => {
      // console.log("heloo");
    });
    // console.log(req);
    if (req.data.status === "success") {
      toast.success(req.data.message);
      setitemNum(req.data.numOfCartItems);
    }
  }

  async function AddditemWishlist(id) {
    let req = await AddWishlist(id).catch((err) => {
      // console.log(err);
    });
    if (req.data.status === "success") {
      toast.success(req.data.message);

      // console.log(req);
    }
  }


  async function addheart(e, id) {
    e.target.classList.replace("fa-regular", "fa-solid");
    
  }
  

  return (
    <div className="mt-5">
      {isLoading ? (
        <div className="loading position-fixed top-0 end-0 bottom-0 start-0 bg-white d-flex justify-content-center align-items-center ">
          <span className="loader "></span>
        </div>
      ) : (
        <>
          <MainCarousel></MainCarousel>
          <CateCarousel></CateCarousel>
          <div className="row g-3 ">
            {data?.data.data.map((e) => {
              return (
                <div
                  key={e.id}
                  className="col-md-3 h-100 position-relative my-4"
                >
                  <div className="product ">
                    <div
                      className=" position-absolute top-0 end-0 p-4"
                      onClick={() => {
                        AddditemWishlist(e.id);
                      }}
                    >
                      <i
                        className="fa-regular fa-heart fs-2 text-danger "
                        onClick={(e) => {
                          addheart(e, e.id);
                        }}
                      ></i>
                    </div>

                    <Link to={"/ProductDetails/" + e.id}>
                      <img src={e.imageCover} alt=" " className="w-100"></img>

                      {/* <img src={e.imageCover} alt="nd" className="w-100"></img> */}
                      <h6 className="text-success">{e?.category?.slug}</h6>
                      <h5>{e.title.split(" ").slice(0, 2).join(" ")}</h5>

                      <div className="d-flex justify-content-between ">
                        <p>{e.price} EGP</p>

                        <span>
                          <i className="fa-solid fa-star text-warning"></i>
                          {e.ratingsAverage}
                        </span>
                      </div>
                    </Link>
                    <div className="">
                      <button
                        className="bg-main text-white btn w-100 d-block"
                        onClick={() => {
                          AddIdCart(e.id);
                        }}
                      >
                        add product
                      </button>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </>
      )}

      <nav aria-label="Page navigation example ">
        <ul className="pagination justify-content-center mt-5">
          <li className="page-item ">
            <Link className="page-link" onClick={pagenumber} pagenumber="1">
              1
            </Link>
          </li>
          <li className="page-item">
            <Link className="page-link" onClick={pagenumber} pagenumber="2">
              2
            </Link>
          </li>
        </ul>
      </nav>
    </div>
  );
}
