import { useState } from "react";
import { createContext } from "react";

export let Usernamecontext = createContext();

export function UserContextprovide({ children }) {
  let [userToken, setToken] = useState(null);
  let [username, setname] = useState("");


  return (
    <>
      <Usernamecontext.Provider value={{ userToken, setToken, username,setname}}>
        {children}
      </Usernamecontext.Provider>
    </>
  );
}
