import React, { useContext, useState } from "react";
import { useFormik } from "formik";
import * as Yup from "yup";
import { useParams } from "react-router-dom";
import { CartContext } from "../context/CartContext";

export default function CheckOut() {
  let { chekout } = useContext(CartContext);
  let data = useParams();

  let [loading, sendloading] = useState(false);

  let validationSchema = Yup.object({
    phone: Yup.string()
      .required("phone Required")
      .matches(/^01[0152][0-9]{8}$/, "enter valid phone"),
    details: Yup.string()
      .required("details Required")
      .matches(/^[\w-]{3,}$/, "enter valid details"),
    city: Yup.string()
      .required("details Required")
      .matches(/^[\w-]{3,}$/, "enter valid city"),
  });

  let formik = useFormik({
    initialValues: {
      city: "",
      phone: "",
      details: "",
    },
    onSubmit: sendcheckout,
    validationSchema,
  });
  async function sendcheckout(val) {
    sendloading(true);
    let req = await chekout(data.id, val);
    sendloading(false);

    window.open(req.data.session.url, "_self");
    // console.log(req);
  }

  return (
    <div className="mt-5 py-5">
      <form onSubmit={formik.handleSubmit}>
        <div className="mb-4">
          <input
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            name="city"
            type="text"
            className="form-control "
            placeholder="Enter City"
          />

          {formik.touched.city && formik.errors.city ? (
            <p className="text-danger">{formik.errors.city}</p>
          ) : (
            ""
          )}
        </div>
        <div className="mb-4">
          <input
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            type="tel"
            className="form-control "
            placeholder="Enter phone"
            name="phone"
          />

          {formik.errors.phone && formik.touched.phone ? (
            <p className="text-danger">{formik.errors.phone}</p>
          ) : (
            ""
          )}
        </div>
        <div className="mb-4">
          <textarea
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            className="form-control "
            placeholder="Enter details"
            name="details"
          ></textarea>
          {formik.errors.details && formik.touched.details ? (
            <p className="text-danger">{formik.errors.details}</p>
          ) : (
            ""
          )}
        </div>
        {loading === true ? (
          <button className="btn btn-success">
            <i className="fa-solid fa-spinner fa-spin text-white"></i>
          </button>
        ) : (
          <button
            disabled={!(formik.isValid && formik.dirty)}
            type="submint"
            className="btn btn-success"
          >
            Pay Now
          </button>
        )}
      </form>
    </div>
  );
}
