import React, { useContext } from "react";
import Navbar from "../Navbar/Navbar";
import { Outlet } from "react-router-dom";
import { CartContext } from "../context/CartContext";
import { useEffect } from "react";
import toast, { Toaster } from "react-hot-toast";
import { Usernamecontext } from "../context/usercontext";

export default function Layout() {
  let { setitemNum, Getusercart } = useContext(CartContext);
  let date = useContext(Usernamecontext);
  useEffect(() => {
    toast.success(date?.username.name);
    if (localStorage.getItem("userdate") != null) {
      Sendnum();
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  async function Sendnum() {
    let req = await Getusercart().catch((err) => {
      // console.log("err");
    });
    if (req?.data?.status === "success") {
      setitemNum(req?.data?.numOfCartItems);
    }
  }

  return (
    <div>
      <Toaster />
      <Navbar />

      <div className="container  py-5">
        <Outlet></Outlet>
      </div>
    </div>
  );
}
